@images: "../img";



body.front {
    #page-header {
        height: (@navbar-height * 6);
        height: 40vh;
        background-position: center top;
        background-image: url("@{images}/background.jpg");
        background-repeat:no-repeat;
        background-size: cover;
    }
}

.navbar-header {
    position:absolute;
    
}
.container-fluid {
    &.navbar {
        border: none;
    }
    .navbar-header > .logo {
        margin-left: 25px;
        background-color: @navbar-default-bg;
        padding-left: @navbar-margin-bottom;
        padding-right: @navbar-margin-bottom;
        padding-bottom: @navbar-margin-bottom;
        border-radius: 0 0 @border-radius-base @border-radius-base;
    }
}
#page-header {
    border-bottom: 1px solid #E5E5E5;
    p.lead {
        float:right;
        color:@brand-primary;
        font-size:@jumbotron-font-size * 3;
        padding-top: @navbar-margin-bottom * 2;
        padding-right: @navbar-margin-bottom * 5;
        
    }
}
.navbar-nav {
    > li > a.active {
      &:hover,
      &:focus {
        background-color: darken(@navbar-default-link-hover-bg, 10%) !important;
      }
    }
}



.menu .navbar-nav {
    > li > a.active {
      &:hover,
      &:focus {
        background-color: darken(@navbar-default-link-hover-bg, 10%) !important;
      }
    }
}




// Search form.
form#islandora-solr-simple-search-form {
  margin: 0;
}
.navbar #block-islandora-solr-simple {
  float: none;
  margin: 5px 0 5px 5px;
  @media @normal {
    float:right; 
    clear:right;
    padding-top: @navbar-margin-bottom * 2;
    padding-right: @navbar-margin-bottom * 5;
  }

  .input-group-btn {
    width: auto;
  }
}

header #block-islandora-solr-simple {
  float: right;
  max-width:500px;
  margin: 5px 5px 5px 5px;
  @media @normal {
    float: right;
    clear:right;
    padding-top: @navbar-margin-bottom / 2;
    padding-right: @navbar-margin-bottom * 5;
  }

  .input-group-btn {
    width: auto;
  }
}
#edit-islandora-simple-search-query {
    background: fadeout(white, 20%)
}

.navbar-search .control-group {
  margin-bottom:0px;
}

footer {
 background: @dcmny-color2; 
 color: white;
  position: relative;
  margin-top: -200px; /* negative value of footer height */
  height: 200px;
  clear:both;
  padding-top:20px;
}


#push {
    height:20px;
}
#wrap {
    min-height: 100%;
    height: auto !important;
    height: 100%;
    /* Negative indent footer by it's height */
    margin-bottom: -200px;
}

    
